import React, { Component } from "react";

class index extends Component {
  constructor(props) {
    super(props);
  }
  componentDidMount() {}
  render() {
    if (!this.props.matchDetails) {
      return false;
    }
    console.log(this.props.matchDetails);
    return <div>Match Details</div>;
  }
}

export default index;
